.video-responsive {
  overflow: hidden;
  padding-bottom: 56.25%;
  position: relative;
  height: 0;
}

.video-responsive video {
  left: 0;
  top: 0;
  /* height: 100%; */
  width: 100%;
  position: absolute;
  background-color: aqua;
}

.video-responsive iframe {
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  position: absolute;
}